<template>
  <div v-if="seasons.length==0">Loading....</div>
  <ul v-else>
    <li v-for="season in seasons">
      <div><strong>{{ season.id }} Temporada</strong></div>
      <div><small>{{ season.releaseDate }}</small></div>
      <button name='seasons' @click="toggle(season.id)" >{{ season.numberOfEpisodes }} Episódios</button>
      <div class='seasons' v-if="season.isActive">
          <Episodes :serieid="serieId" :seasonid="season.id" />
      </div>
    </li>
  </ul>
</template>

<script>
import Episodes from '@/components/Episodes.vue'

export default {
  name: 'Seasons',
  components: {
    Episodes
  },
  data () {
    return {
      seasons: [],
      serieId: this.serieid
    }
  },
  props: {
    serieid: String
  },
  methods: {
    loadSeasons () {
        this.$http
            .get('/series/' + this.serieId + '/seasons?mockDate=2019-03-01')
            .then(res => {
              this.seasons = res.data
            })
    },
    toggle (id) {
      this.seasons = this.seasons.map(season => {
        if (season.id == id) {
          season.isActive = !season.isActive
        }
        return season
      })
    }
  },
  mounted () {
    this.loadSeasons()
  },
}
</script>

