<template>
  <div v-if="episodes.length==0">Loading....</div>
  <ul v-else>
    <li v-for="episode in episodes">
      <strong>{{ episode.id }} Episódio</strong> | 
      <strong>{{ episode.title }}</strong> | 
      <small>IMDB: {{ episode.imdbId }}</small> | 
      <small>{{ episode.releaseDate }}</small> | 
      {{ episode.duration }}
    </li>
  </ul>
</template>

<script>
import moment from 'moment';

export default {
  name: 'Episodes',
  data () {
    return {
      episodes: [],
      serieId: this.serieid,
      seasonId: this.seasonid
    }
  },
  props: {
    serieid: String,
    seasonid: String
  },
  methods: {
    loadEpisodes () {
        this.$http
            .get('/series/' + this.serieId + '/seasons/' + this.seasonId + '/episodes?mockDate=2019-03-01')
            .then(res => {
              this.episodes = res.data
            })
    },

  },
  filters: {
    formatDate (value) {
      if (value) {
        return moment(String(value)).format('DD/MM/YYYY', true)
      }
    }
  },
  mounted () {
    this.loadEpisodes()
  },
}
</script>

