<template>
  <div class="calculator">
    <router-link to="/">Voltar</router-link>
    <h1>Calculadora</h1>
    <h2>{{ serie.title }} </h2>
    <form>
      <div class="time">
        <div>Mock day: 2019-03-01</div>
        <div>Próxima temporada: {{   novo }}</div>
      </div>
      <div class="time">
        <label>Quando quero começar</label><br />
        <datepicker 
          placeholder="dd/mm/aaaa" 
          :format="'dd/MM/yyyy'" 
          :value="initial_date" ></datepicker>
      </div>
      <div class="time">
        <label>Tempo para assistir por dia</label><br />
        <input type="text" name='time_per_day' id='time_per_day' v-model="time_per_day" />
      </div>
      <div>
        <button @click.prevent="result" >Calcular</button>
      </div>
    </form>

    <div v-show="status != ''">
      <h1>Resultado</h1>
      <div class='message' v-if="status == 'success'">
        <p class='success' >
          <div class='title'>Fique pronto para próxima temporada !</div>
          <div class='description'>voçê deverá começar a assistir em: <strong>aaaaa</strong></div>
        </p>
      </div>
      <div class='message' v-else-if="status == 'error'">
        <p class='error' >
          <div class='title'>Lamentamos...</div>
          <div class='description'>Infelizmente não será possível toda a temporada antes da próxima temporada</div>
        </p>
      </div>
    </div>

  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
import Duration from 'duration-js';
import moment from 'moment';

export default {
  name: 'Calculator',
  components: {
    Datepicker
  },
  data () {
    return {
      serie: [],
      seasons: [],
      episodes: [],
      status: '',
      initial_date: '',
      time_per_day: '24',
      nextSeason: ''
    }
  },
  methods: {
    
    result () {
      this.status = 'success'
    },
    loadSeasons (serieId) {
        this.$http
            .get('/series/' + serieId + '/seasons?mockDate=2019-03-01')
            .then(res => {
              this.seasons = res.data
            })
    },
    loadEpisodes (serieId, seasonId) {
        this.$http
            .get('/series/' + serieId + '/seasons/' + seasonId+ '/episodes?mockDate=2019-03-01')
            .then(res => {
              this.episodes = res.data
            })
    },
    timeDiff (seasons) {
      let datas = []
      let startDate = ''
      for (let season in seasons) {
        let dataSeason = moment(seasons[season].releaseDate)
        let today = moment().format('YYYY-MM-DD')
        
        if (moment(today).isSameOrBefore(dataSeason)) {
          this.loadEpisodes(this.serie.id, season)
          let days = dataSeason.diff(today, 'days')
          let hours = dataSeason.diff(today, 'hours')

          // total time to watch
          let durationTotal = 0
          for (let duration in this.episodes) {
            let d = this.episodes[duration].duration
            d = d.substring(d.length - 2, 0).replace(' ','')
            durationTotal += new Duration(d)
          }
          durationTotal = new Duration(durationTotal).minutes() / 60

          // hours per day
          let daysToWatch = Math.ceil(durationTotal / this.time_per_day)

          // Begin date
          startDate = moment(dataSeason.format("YYYY-MM-DD")).subtract(daysToWatch, 'days').format('YYYY-MM-DD')

          this.nextSeason = dataSeason

          datas.push(days, hours, durationTotal, daysToWatch, dataSeason.format('DD/MM/YY'), startDate)
        }
      }
      this.initial_date = startDate
      return datas
    },
    
    
  },

  computed: {
    novo () {
      this.timeDiff(this.seasons)
    }
  },

  created () {
    this.serie = this.$route.params.serie
    this.loadSeasons (this.serie.id)
  },

  filters: {
    formatDate (value) {
      if (value) {
        return moment(String(value)).format('DD/MM/YYYY', true)
      }
    }
  }

}

</script>

<style>
  .time {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>
