<template>
  <div v-if="series.length==0">Loading....</div>
  <ul v-else>
    <li v-for="(serie, index) in series">
      <strong>{{ serie.title }}</strong>
      <div><small>Lançado: {{ serie.releaseDate}}</small></div>
      <div><small>IMDB: {{ serie.imdbId}}</small></div>
      <button name='seasons' @click="toggle(serie.id)" >{{ serie.numberOfSeasons}} Temporadas</button>
      <div class='seasons' v-if="serie.isActive">
          <Seasons :serieid="serie.id" />
      </div>
      <router-link :to="{name: 'calculator', params: {id: serie.id, serie: serie}}">Assista novamente</router-link>
    </li>
  </ul>
</template>

<script>
import Seasons from '@/components/Seasons.vue'

export default {
  name: 'Serie',
  components: {
    Seasons
  },
  data () {
    return {
      series: [],
    }
  },
  methods: {
    loadSeries () {
      this.$http
          .get('/series?mockDate=2019-03-01')
          .then(res => {
            this.series = res.data
          })
    },
    toggle (id) {
      this.series = this.series.map(serie => {
        if (serie.id == id) {
          serie.isActive = !serie.isActive
        }
        return serie
      })
    },
  },
  mounted () {
    this.loadSeries()
  }
}
</script>

