import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Calculator from './views/Calculator.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/calculator/:id',
      name: 'calculator',
      props: true,
      component: Calculator
    }
  ]
})
